import React from "react";
import { FaGithub, FaRocket } from "react-icons/fa";

function anotherprojects(props) {
  return (
    <div className="col d-card-2 m-2">
      <figure
        className={props.imageClass}
        alt={props.title}
        style={{ maxWidth: "70rem" }}
      >
        <h3 style={{ fontSize: "3rem" }}>{props.title}</h3>
        <a
          className="nav-link fs-2 nav-link-2"
          href={props.githubLink}
          target="_blank"
          rel="noreferrer"
        >
          <FaGithub />
          GITHUB
        </a>
        <a
          className="nav-link fs-2 nav-link-2"
          href={props.deployedSite}
          target="_blank"
          rel="noreferrer"
        >
          <FaRocket />
          Deployed Site
        </a>
        <div className="descrip-2">
          <h4>Concept and Technologies</h4>
          <ul>
            <li>
             {props.concepts}
            </li>
            <li>
              {props.technologies}
            </li>
          </ul>
        </div>
      </figure>
    </div>
  );
}

export default anotherprojects;
