import React from "react";
import projects from "./projects.json";
import AProjects from "../anotherprojects";
import { FaGithub, FaRocket } from "react-icons/fa";


function Works() {
  console.log("This is the current page", window.location.href);
  return (
    <>
      <section className="container-flex about-me work-row">
        <div className="about-title work-title">
          <h2 className="work-header" style={{ fontSize: " 4rem" }}>
            Works
          </h2>
        </div>
        {projects.map((e, index) =>
          index === 0 ? (
            <div className="work-picture d-parent" key={index}>
              <h2>Most Recent Work</h2>
              <div className="d-card">
                <figure
                  className={e.className}
                  id="work"
                  alt="Do you even funko"
                >
                  <h3 id="most-recent-title" style={{ fontSize: "6rem" }}>
                    {e.title}
                  </h3>
                  <a
                    className="nav-link custom-link"
                    href={e.githubLink}
                    target="_blank"
                    rel="noreferrer"
                  >
                    <FaGithub />
                    GITHUB
                  </a>
                  <a className="nav-link custom-link" href={e.deployedSite}>
                    <FaRocket />
                    Deployed Page
                  </a>
                  <div className="m-5 d-flex descrip">
                    <h4>Concept and Technologies</h4>
                    <ul>
                      <li className="fs-3">{e.concepts}</li>
                      <li className="fs-3">{e.technologies}</li>
                    </ul>
                  </div>
                </figure>
              </div>
            </div>
          ) : (
            <div className="container m-0 d-parent">
              <div className="row">
                <AProjects
                  imageClass={e.className}
                  title={e.title}
                  githubLink={e.githubLink}
                  deployedSite={e.deployedSite}
                  key={index}
                  concepts={e.concepts}
                  technologies={e.technologies}
                />
              </div>
            </div>
          )
        )}
      </section>
    </>
  );
}

export default Works;
